/**
 * Popup Function
 * popup(type, [string|obj])
 *
 * @param {String} type 按鈕呈現的四種型式（必填），['alert', 'confirm', 'warning', 'error']，[單按鈕, 雙按鈕（確認綠色）, 雙按鈕（確認紅色）]
 * @param {String} title 標題文字（必填）
 * @param {String} text 內容敘述（選填）
 * @param {String} confirmButtonText 確認按鈕文字
 * @param {String} cancelButtonText 取消按鈕文字（單按鈕只有確認按鈕沒有取消）
 * @param {Boolean} clickClose 點擊外圍是否會關閉（等同取消），預設可關閉(true)
 *
 * @example 1. 單按鈕（綠色）
 * popup('alert', '標題');
 * popup('alert', '標題', '內容敘述');
 * popup('alert', '標題').then( function() { ... })
 * 
 * @example 2. 單按鈕（紅色）
 * popup('error', '標題')
 * popup('error', '標題', '內容敘述');
 * 
 * @example 3. 雙按鈕（確認綠色）
 * popup('confirm', '標題')
 * popup('confirm', '標題', '內容敘述');
 * 
 * @example 4. 雙按鈕（確認紅色）
 * popup('warning', '標題')
 * popup('warning', '標題', '內容敘述');
 * 
 * @example 5. 詳細設定
 *  popup('warning', {
 *  	title: '標題',
 *  	text: '內容敘述',
 *  	confirmButtonText: '右邊紅色按鈕文字',
 *  	cancelButtonText: '左邊鏤空取消按鈕文字',
 *  	clickClose: false // 點擊Popup外圍是否會關閉彈窗
 *  }).then( function(result) {
 *  	if (result.ok) {
 *  		// 選擇確認按鈕（單雙按鈕都有）
 *  		console.log('Popup then: 確認')
 *  	} else if (result.cancel) {
 *  		// 選擇取消按鈕
 *  	} else {
 *  		// 其它方式關閉
 *  		console.log('Popup then: Any Other')
 *  	}
 *  });
 * 
 */

import { createElementFromHTML } from './fn.mjs'

// 為了要符合IE 11，要解決Promise is not defined 問題
import es6Promise from 'es6-promise';
var Promise = es6Promise.Promise;

(function(window) {
	const html = '\
		<div class="reveal reveal-msg" data-reveal="">\
			<h3 class="reveal-msg__title text-center">標題</h3>\
			<p class="reveal-msg__desc text-center"><small>敘述</small></p>\
			<div class="button-box grid-x">\
			</div>\
		</div>\
	';

	const btnConfirm = '<button class="btn btn-confirm" type="button" data-close="">確認</button>'; // 綠色按鈕
	// const btnWarning = '<button class="btn alert" type="button" data-close="">確認</button>'; // 紅色按鈕
	const btnCancel = '<button class="btn hollow btn-cancel" data-close="" aria-label="關閉彈窗" type="button">取消</button>'

	class PopupModule {
		constructor(type, ...args) {
			// Prevent run in Node env
			if (typeof window === 'undefined') {
				return false
			}

			// [START] Init Popup
			// 設定 Default 變數
			const defaultOpt = {
				title: '標題',
				text: null,
				confirmButtonText: null, // 確認、右邊按鈕文字
				cancelButtonText: null, // 取消、左邊按鈕文字
				clickClose: true // 點擊Popup外圍是否會關閉彈窗
			}

			// 處理帶入的變數
			let argsOpt = {}
			if (args.length > 1) {
				argsOpt = {
					title: args[0],
					text: args[1],
				}
			} else if (typeof args[0] === 'string') {
				argsOpt = {
					title: args[0]
				}
			} else {
				argsOpt = args[0];
			}

			const opt = {
				...defaultOpt,
				...argsOpt
			}

			// Promise 變數
			this._result = null;
			this._resolve = null;
			this._reject = null;

			return this._init(type, opt);
		}

		_init (type, opt) {
			// 定義DOM內容
			const el = createElementFromHTML(html);
			const $el = $(el);

			// 標題
			el.querySelector('.reveal-msg__title').innerHTML = opt.title;

			// 內容
			const descEl = el.querySelector('.reveal-msg__desc');
			if (opt.text) {
				descEl.innerHTML = opt.text;
			} else {
				descEl.remove();
			}

			// 按鈕
			const btnBox = el.querySelector('.button-box');
			const btnCancelEl = createElementFromHTML(btnCancel);
			const btnConfirmEl = createElementFromHTML(btnConfirm);
			switch (type) {
			case 'confirm':
				// 只兩個按鈕，左取消，右確認（綠色）
				btnBox.appendChild(btnCancelEl);
				btnBox.appendChild(btnConfirmEl);
				break;
			case 'warning':
					// 只兩個按鈕，左取消，右確認（紅色）
					btnBox.appendChild(btnCancelEl);
					btnBox.appendChild(btnConfirmEl);
					btnConfirmEl.classList.add('alert');
					break;
			case 'error':
				// 只兩個按鈕，左取消，右確認（紅色）
				btnBox.appendChild(btnConfirmEl);
				btnConfirmEl.classList.add('alert');
				btnConfirmEl.classList.add('w-full');
				break;
			default:
				// 只有一按鈕「確認」
				btnBox.appendChild(btnConfirmEl);
				btnConfirmEl.classList.add('w-full');
				break;
			}

			const btnCancelElCurrent = btnBox.querySelector('.btn-confirm');
			const btnConfirmElCurrent = btnBox.querySelector('.btn-cancel'); 

			// 按鈕 - 確認
			if (opt.confirmButtonText && btnCancelElCurrent) {
				btnCancelElCurrent.innerHTML = opt.confirmButtonText;
			}

			// 按鈕 - 取消
			if (opt.cancelButtonText && btnConfirmElCurrent) {
				btnConfirmElCurrent.innerHTML = opt.cancelButtonText
			}

			// ================================

			var domCache = {
				el: el,
				$el: $el,
				confirmButton: btnCancelElCurrent,
				cancelButton: btnConfirmElCurrent
			}

			// ================================

			// 生成 Popup (Foundation)
			const popupOpt = {
				closeOnClick: opt.clickClose,
				closeOnEsc: opt.clickClose,
			}

			var elem = new Foundation.Reveal($el, popupOpt);

			// 定義關閉事件 => 刪除 DOM
			$el.on('closed.zf.reveal', function() {
				var $this = $(this);
				setTimeout(() => {
					$this.parent().remove();
				}, 100);
			});

			// 開啟 Popup
			$el.foundation('open');
			// [END] Init Popup

			// ================================

			// [START] Promise
			return this._result = this._promise(domCache)
			// [END] Promise
		}

		// Promise 思路參考(Angular)：
		// https://www.grapecity.com/blogs/implementing-modal-dialog-functions-promise-based-dialog-results-angular
		_promise (domCache) {
			return new Promise((resolve, reject) => {
				this._resolve = resolve;
				this._reject = reject;

				var result = {
					ok: false, // 是否選擇確認
					cancel: false, // 是否選擇取消
				}

				if (domCache.confirmButton) {
					domCache.confirmButton.addEventListener('click', function() {
						result.ok = true
						resolve(result);
					});
				}

				if (domCache.cancelButton) {
					domCache.cancelButton.addEventListener('click', function() {
						result.cancel = true
						resolve(result);
					});
				}

				domCache.$el.on('closed.zf.reveal', function() {
					resolve(result);
				});
			})
		}
	}

	function popup (type, ...args) {
		let popupEl;
		if (args.length === 1) {
			popupEl = new PopupModule(type, args[0])
		} else {
			popupEl = new PopupModule(type, args[0], args[1])
		}
	
		return popupEl;
	}
	
	window.popup = popup;

})(window);

