/*global EaseScroll, lazyload*/

'use strict';

import { on, off, throttle, isInViewport } from './modules/fn.mjs';
import './modules/ie-object-fit.mjs';
import './modules/lazyload.mjs';
import './modules/webp-support.mjs';
